::-webkit-scrollbar {
    display: none;
}

input, button {
    height: 24px;
}

h1, h4 {
    text-align: center;
    margin: 0;
}

img {
    display: inline-block;
    max-width: 100%;
}

.container {
    display: grid;
    justify-content: center;
    grid-template-areas: 'title' 'search' 'cards';
    /* align-content: center; */
}

.title {
    grid-area: title;
}

.search {
    grid-area: search;
}

.cards {
    grid-area: cards;
    display: grid;
    grid-template-columns: repeat(4, minmax(200px, 1fr));
    gap: 1em;
}

figure {
    display: grid;
    justify-items: center;
    align-items: center;
    text-align: center;
    margin: 0;
    margin-top: 20px;
    background-color: #F5F5F5;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    padding: 0;
}

ul {
    list-style: none;
}

li {
    font-size: 16px;
}

.modal-detail {
    background-color: aqua;
}

.modal {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    text-align: left;
    background: rgba(0,0,0, .5);
    transition: opacity .25s ease;
}

.modal-inner {
    transition: top .25s ease;
    position: absolute;
    top: 0%;
    right: 0;
    bottom: 0;
    left: 0;
    width: 70%;
    margin: auto;
    overflow: auto;
    background: #fff;
    border-radius: 5px;
    padding: 1em 2em;
    height: max-content;
}

 .close {
    grid-area: close;
    height: min-content;
    width: min-content;
    background-color: rgb(248, 87, 87);
    border-style: none;
    padding: 5px 18px;
    border-radius: 3px;
    margin-top: 10px;
    margin-bottom: 10px;
    /* float: right; */
}

  .detail-title {
    text-align: center;
    grid-area: title;
    margin-bottom: 10px;
    height: min-content;
  }

  .poster {
    grid-area: poster;
    height: 300px;
  }

  .details {
    font-size: 1.2rem;
    margin: 0;
    display: grid;
    justify-items: center;
    grid-template-areas: 'poster detail';
    /* align-content: space-between; */
  }

  .detail {
    grid-area: detail;
  }

  li {
    margin: 10px 0;
  }


/* Responsive Breakpoint */
/* Tablet */
@media screen and (max-width: 840px) {
    .cards {
        grid-area: cards;
        display: grid;
        grid-template-columns: repeat(3, minmax(150px, 1fr));
        gap: 1em;
    }
}

/* Mobile */
@media screen and (max-width: 560px) {
    .details {
        overflow-y: scroll;
        font-size: 1.2rem;
        margin: 0;
        display: grid;
        grid-template-areas: 'poster' 'detail';
        /* align-content: space-between; */
    }

    .cards {
        grid-area: cards;
        display: grid;
        grid-template-columns: repeat(2, minmax(150px, 1fr));
        gap: 1em;
    }

    .modal-inner {
        transition: top .25s ease;
        position: absolute;
        top: 0%;
        right: 0;
        bottom: 0;
        left: 0;
        width: 70%;
        margin: auto;
        overflow: auto;
        background: #fff;
        border-radius: 5px;
        height: max-content;
    }

    .close {
        grid-area: close;
        height: min-content;
        width: min-content;
        background-color: rgb(248, 87, 87);
        border-style: none;
        padding: 5px 18px;
        border-radius: 3px;
        margin-top: 30px;
        margin-bottom: 10px;
        /* float: right; */
    }

    .detail-title {
        text-align: center;
        grid-area: title;
        margin-bottom: 10px;
        height: min-content;
        font-size: 18px;
      }
}